.features-service {
  position: relative;
  width: 100vw;
  height: 670px;

  &__content {
    position: relative;
    left: 836px;
    top: 135px;
  }

  &__title {
    width: 560px;
    height: 148px;
    color: $text-title;
    font-family: Georgia, sans-serif;
    font-size: 60px;
    line-height: 74px;

    padding-bottom: 24px;
  }

  &__text {
    height: 203px;
    width: 462px;
    font-family: sans-serif;
    font-size: 16px;
    line-height: 29px;
    padding-bottom: 56px;
    color: $text;
  }

  &__formButton {
    display: inline-block;
  }

  &__button {
    height: 62px;
    width: 215px;

    background: white;
    border-radius: 31px;
    border: 1px solid #2d415e;
    transition: 0.5s;

    &:hover {
      transform: scale(1.1);
      transition: 0.5s;
    }

    &--active {
      height: 62px;
      width: 215px;
      margin-right: 20px;
      background: $button-active;
      border-radius: 31px;
      border: solid $button-active;
      transition: 0.5s;

      &:hover {
        transform: scale(1.1);
        transition: 0.5s;
      }
    }
  }

  &__image {
    position: absolute;
    left: 100px;
    top: 116px;
  }
}
